@import "_variables.scss";
@import "_mixins.scss";

.cards {
  @include centered-justify;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;
}

.card-wrapper {
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  margin: 0 24px 50px;
  padding: 30px 14px 15px;
}

.img {
  width: 160px;
  height: 134px;
  display: block;
  margin: 0 auto;
}

.card-information__title {
  margin-top: 45px;
}

.card-information__title-iMac {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  margin-bottom: 5px;
}

.card-information__reviews {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
}

.card-information__reviews-stars {
  display: flex;

  .star-active {
    width: 15px;
    height: 15px;
    background: url("../images/star-active.svg") center no-repeat;
    margin-right: 4px;
  }

  .star:nth-child(n+5) {
    background-image: url("../images/star.svg");
  }
}

.card-information__reviews-number,
.card-information__price-cost,
.card-information__title-product {
  font-size: 11px;
}

.card-information__price {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-top: 20px;
}

.card-information__price-number {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}

.card-information__button {
  font-family: $font;
  width: 170px;
  height: 40px;
  background: $content-button-color;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  margin-top: 20px;

  &:hover {
    color: $content-button-color;
    background-color: #fff;
    border: 1px solid $content-button-color;
    cursor: pointer;
    transition: color 500ms ease-in-out;
  }
}
